import React, { Component } from "react"
import { compose, withProps, lifecycle } from "recompose"
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps"


const MyMapComponent = compose(
    withProps({
        googleMapURL: "https://maps.googleapis.com/maps/api/js?key=AIzaSyA948FGanlWuLabr-lrpU9R7L968FH5Olo&libraries=geometry,drawing,places",
        loadingElement: <div style={{ height: `100%` }} />,
        containerElement: <div style={{ height: `400px` }} />,
        mapElement: <div style={{ height: `100%` }} />,
    }),
    lifecycle({
        
        componentWillMount() {
            const refs = {}

            this.setState({
                position: null,
                onMarkerMounted: ref => {
                    refs.marker = ref;
                    
                },

                onPositionChanged: () => {
                    const position = refs.marker.getPosition();
                }
            })
        },
    }),
    withScriptjs,
    withGoogleMap
)((props) =>
    <GoogleMap defaultZoom={8} defaultCenter={{ lat: -34.397, lng: 150.644 }}>
        {props.isMarkerShown && <Marker position={{ lat: -34.397, lng: 150.644 }} draggable={true} ref={props.onMarkerMounted} onPositionChanged={props.onPositionChanged} name={"Marker"} />}
    </GoogleMap>
    )

class Map extends Component {
    state = {
        isMarkerShown: false,
    }


    render() {
        return (
            <div style={{marginTop:"80px",padding:"0 5% 0 5%"}}>
                <MyMapComponent isMarkerShown={true} />
            </div>
        )
    }
}


export default Map;
